import { useEffect, useState } from "react";
import {
  MapContainer,
  TileLayer,
  useMap,
  Marker,
  useMapEvent,
  Popup,
} from "react-leaflet";
import Select from "react-select";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import "leaflet.heat";

// Custom icon for markers with transparent background
const createLabelIcon = (text) => {
  return L.divIcon({
    className: "fraud-label",
    html: `<div style="display: inline-flex; color: white; font-weight: bold; text-align: center; align-items: center; background: rgba(0,0,0,${
      text / 3
    }); padding: 8px 15px 8px 15px; border-radius: 50px;">${text}</div>`,
  });
};

const HeatmapLayer = ({ points }) => {
  const map = useMap();

  useEffect(() => {
    if (points.length === 0) return;

    const heatLayer = L.heatLayer(
      points.map(({ lat, lng }) => [lat, lng, 0.7]), // Normalize intensity
      {
        radius: 25,
        gradient: {
          0.0: "yellow",
          0.25: "#FFAA33",
          0.5: "orange",
          0.75: "red",
          "1.0": "#DC143C",
          // 0.2: "#440154",
          // 0.4: "#3b528b",
          // 0.6: "#21908d",
          // 0.8: "#5dc963",
          // 1.0: "#fde725",
        },
        blur: 0,
        maxZoom: 8,
      }
    );

    heatLayer.addTo(map);
    return () => map.removeLayer(heatLayer);
  }, [map, points]);

  return null;
};

// Group data by location
const groupDataByLocation = (data) => {
  const grouped = {};
  data.forEach(({ lat, lng, Location, Type, Offense, Highlight }) => {
    const key = `${lat},${lng}`;
    if (!grouped[key]) {
      grouped[key] = {
        lat,
        lng,
        Location,
        totalCases: 0,
        types: new Set(),
        offenses: new Set(),
        highlights: [],
      };
    }
    grouped[key].totalCases += 1;
    grouped[key].types.add(Type);
    Offense.split(",").forEach((off) => {
      const _offence = off.trim().toLowerCase();
      if (_offence.length > 0) grouped[key].offenses.add(_offence);
    });
    if (Highlight) grouped[key].highlights.push(Highlight);
  });

  return Object.values(grouped);
};

// Detect overlapping markers dynamically
const detectOverlappingMarkers = (data, zoomLevel) => {
  let threshold = 1;
  if (zoomLevel > 4) threshold = 0.5
  if (zoomLevel > 7) threshold = 0.2;
  if (zoomLevel > 8) threshold = 0;
  const groupedMarkers = [];
  const visited = new Set();

  data.forEach((marker, index) => {
    if (visited.has(index)) return;

    const closeMarkers = [marker];
    visited.add(index);

    // Check for nearby markers
    data.forEach((otherMarker, otherIndex) => {
      if (index !== otherIndex && !visited.has(otherIndex)) {
        const distance = Math.sqrt(
          Math.pow(marker.lat - otherMarker.lat, 2) +
            Math.pow(marker.lng - otherMarker.lng, 2)
        );
        if (distance < threshold) {
          closeMarkers.push(otherMarker);
          visited.add(otherIndex);
        }
      }
    });

    if (closeMarkers.length > 1) {
      // Combine markers
      const combinedLat =
        closeMarkers.reduce((sum, m) => sum + m.lat, 0) / closeMarkers.length;
      const combinedLng =
        closeMarkers.reduce((sum, m) => sum + m.lng, 0) / closeMarkers.length;
      groupedMarkers.push({
        lat: combinedLat,
        lng: combinedLng,
        isCombined: true,
        locations: closeMarkers.map((m) => m.Location).join(", "),
        totalCases: closeMarkers.reduce((sum, m) => sum + m.totalCases, 0),
        casesList: closeMarkers.map((m) => `${m.Location}: ${m.totalCases} cases`),
        types: new Set(),
        offenses: new Set(),
        highlights: [],
      });
    } else {
      groupedMarkers.push({ ...marker, isCombined: false });
    }
  });

  return groupedMarkers;
};

// Detect zoom change and update markers
const ZoomHandler = ({ data, setDisplayedMarkers }) => {
  const map = useMapEvent("zoomend", () => {
    const newZoom = map.getZoom();
    setDisplayedMarkers(detectOverlappingMarkers(data, newZoom));
  });

  useEffect(() => {
    setDisplayedMarkers(detectOverlappingMarkers(data, map.getZoom()));
  }, [data, map]);

  return null;
};

const Map = ({ data }) => {
  const [groupedData, setGroupedData] = useState(groupDataByLocation(data));
  const [filteredData, setFilteredData] = useState(data);
  const [selectedFraudTypes, setSelectedFraudTypes] = useState([]);
  const [selectedSubjects, setSelectedSubjects] = useState([]);
  const [displayedMarkers, setDisplayedMarkers] = useState([]);

  // Extract unique options for filters
  const fraudTypeOptions = [...new Set(data.map((d) => d.Type))].map(
    (type) => ({ value: type, label: type })
  );
  const subjectOptions = [...new Set(data.map((d) => d["Subjected on"]))].map(
    (subject) => ({
      value: subject,
      label: subject,
    })
  );

  // Apply filters
  useEffect(() => {
    let filtered = data;

    if (selectedFraudTypes.length > 0) {
      const selectedTypes = selectedFraudTypes.map((opt) => opt.value);
      filtered = filtered.filter((d) => selectedTypes.includes(d.Type));
    }

    if (selectedSubjects.length > 0) {
      const selectedSubjectedOn = selectedSubjects.map((opt) => opt.value);
      filtered = filtered.filter((d) =>
        selectedSubjectedOn.includes(d["Subjected on"])
      );
    }

    setFilteredData(filtered);
    setGroupedData(groupDataByLocation(filtered));
  }, [selectedFraudTypes, selectedSubjects]);

  return (
    <>
      {/* Multi-Select Filters */}
      <div className="flex gap-4 z-50 relative">
        <Select
          isMulti
          options={fraudTypeOptions}
          value={selectedFraudTypes}
          onChange={setSelectedFraudTypes}
          placeholder="Filter by Type of Fraud"
          className="w-1/2"
          styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
          menuPortalTarget={document.body}
        />
        <Select
          isMulti
          options={subjectOptions}
          value={selectedSubjects}
          onChange={setSelectedSubjects}
          placeholder="Filter by Subjected-On"
          className="w-1/2"
          styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
          menuPortalTarget={document.body}
        />
      </div>
      <MapContainer
        center={[20.5937, 78.9629]}
        zoom={5}
        className="h-[600px] w-full rounded-lg shadow-lg"
      >
        {/* Dark and minimal basemap */}
        <TileLayer url="https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png" />
        {/* <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" /> */}
        {filteredData.length > 0 && <HeatmapLayer points={filteredData} />}

        <ZoomHandler data={groupedData} setDisplayedMarkers={setDisplayedMarkers} />

        {/* Add Labels to Show Fraud Numbers */}
        {displayedMarkers.map(
          (
            {
              lat,
              lng,
              isCombined,
              locations,
              totalCases,
              casesList,
              Location,
              cases,
              types,
              offenses,
              highlights,
            },
            index
          ) => (
            <Marker
              key={index}
              position={[lat, lng]}
              icon={createLabelIcon(totalCases)}
            >
              <Popup>
                <div className="p-1 max-h-52 overflow-auto">
                  <div className="p-3">
                    <h3 className="font-bold text-lg">
                      {isCombined
                        ? `Combined Locations: ${locations}`
                        : Location}
                    </h3>
                    <p>
                      <strong>Total Cases:</strong>{" "}
                      {isCombined ? totalCases : cases}
                    </p>
                    {isCombined ? (
                      <ul className="mt-2 text-xs text-gray-700">
                        {casesList.map((item, i) => (
                          <li key={i} className="mb-1">
                             {item}
                          </li>
                        ))}
                      </ul>
                    ) : (
                      <>
                        <p>
                          <strong>Fraud Types:</strong>{" "}
                          {Array.from(types).join(", ")}
                        </p>
                        <p>
                          <strong>Offenses:</strong>{" "}
                          {Array.from(offenses).join(", ")}
                        </p>
                        {highlights.length ? (
                          <>
                            <h2 className="text-base font-bold">
                              Highlights of the cases
                            </h2>
                            <ul className="mt-2">
                              {highlights.map((highlight, i) => (
                                <li key={i} className="text-sm text-gray-700">
                                  - {highlight}
                                </li>
                              ))}
                            </ul>
                          </>
                        ) : null}
                      </>
                    )}
                  </div>
                </div>
              </Popup>
            </Marker>
          )
        )}
      </MapContainer>
    </>
  );
};

export default Map;
